//post
function post(url, data) {
  return new Promise((resolve, reject) => {
    apiPost(url, data).then(
      (value) => {
        if (value.code == 0) {
          resolve(value);
        } else {
          reject(value);
        }
      },
      (reason) => {
        reject(reason);
      }
    );
  });
}
//get
function get(url) {
  return new Promise((resolve, reject) => {
    apiGet(url).then(
      (value) => {
        if (value.code == 0) {
          resolve(value);
        } else {
          reject(value);
        }
      },
      (reason) => {
        reject(reason);
      }
    );
  });
}
